<template>
    <el-tabs  class='film_cao'>
        



        <el-tab-pane label="/">
            <el-form :inline="true" :model="film_select"  class="demo-form-inline">
                <el-form-item label="">
                    <el-input v-model="film_select.name"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit()"></el-button>
                </el-form-item>
            </el-form>
            <el-col :span='12'>
                <div class="select_info">
                    <img :src="film_select_res.pimage" alt="">
                    <div>
                        <p> {{film_select_res.pname}}</p>
                        <p> {{film_select_res.cid}}</p>
                        <p> {{film_select_res.planguage}}</p>
                        <p> {{film_select_res.pishot}}</p>
                        <p> {{film_select_res.length}}</p>
                        <p> {{film_select_res.ticketprice}}</p>
                        <p> {{film_select_res.pdesc}}</p>
                    </div>
                </div>
            </el-col>
            <el-col :span='11' style="margin-top:-30px">
                <el-form :model="ruleForm"  ref="ruleForm"  label-width="100px" class="demo-ruleForm">
                    <el-form-item label="" prop="pname1">
                        <el-input v-model="ruleForm.pname1"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="pname2">
                        <el-input v-model="ruleForm.pname2"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="cid">
                        <el-select v-model="ruleForm.cid" placeholder="">
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                            <el-option label="" value="3"></el-option>
                            <el-option label="" value="4"></el-option>
                            <el-option label="" value="5"></el-option>
                            <el-option label="" value="6"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="planguage">
                        <el-select v-model="ruleForm.planguage" placeholder="">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="pishot">
                        <el-select v-model="ruleForm.pishot">
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="length">
                        <el-input v-model.number="ruleForm.length"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="ticketprice">
                        <el-input v-model.number="ruleForm.ticketprice"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="pdesc">
                        <el-input type="textarea" v-model="ruleForm.pdesc"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                        <el-button @click="resetForm('ruleForm')"></el-button>
                    </el-form-item>
                </el-form>
            </el-col> 
            <el-col :span="1">

            </el-col>
        </el-tab-pane>




        <el-tab-pane label="">
            <form action="http://111.230.128.231:8080/TTMS/playAdd" method="post" class="form-insert" enctype="multipart/form-data">
                <p><span></span><input type="text" name="pname" placeholder=""></p>
                
                <p><span></span><input type="file" name="pimage"></p>
                <p><span></span><select name="cid" id="">
                    <option value="1"></option>
                    <option value="2"></option>
                    <option value="3"></option>
                    <option value="4"></option>
                    <option value="5"></option>
                    <option value="6"></option>
                </select></p>
                <p><span></span><select name="planguage" id="">
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select></p>
                <p><span></span><select name="pishot" id="">
                    <option value="1"></option>
                    <option value="0"></option>
                </select></p>
                <p><span></span><input type="text" name="length"></p>
                <p><span></span><input type="text" name="ticketprice"></p>
                <p><span></span><textarea maxlength="255" cols="100" rows='5' name="pdesc"></textarea></p>
                <input type="submit" value="">
            </form>
        </el-tab-pane>
    </el-tabs>
</template>

<script>
export default {
    data(){
        var checkTime = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            }
            setTimeout(() => {
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                if (value < 0) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            }
            }, 1);
        };
        var checkPrice = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            }
            setTimeout(() => {
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
               if (value < 0) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            }
            }, 1);
        };
        return{
            param:"",
            src:'',
            activeName: 'second',
            film_select:{
                name:'',
            },
            film_select_res:{
                pname:'',
                pimage:'../../../static/7e1f6616b82f532e167a730452c90b9d653258.png',
                cid:'',
                planguage:'',
                pishot:'',
                length:'',
                ticketprice:'',
                pdesc:'',
            },
            ruleForm: {
                pname1:'',
                pname2:'',
                cid: '',
                planguage: '',
                pishot: '',
                length: '',
                ticketprice: '',
                pdesc: ''
            },
            
        }
    },
    methods: {
        onSubmit(){
            //
            var that=this;
            if(this.film_select.name!=''){
                this.$axios({
                    post:'GET',
                    url:'http://111.230.128.231:8080/TTMS/play?method=findPlayByWord',
                    params:{
                        name:that.film_select.name
                    }
                }).then(function(response){
                    that.film_select_res=response.data[0];
                }).catch(function(response){
                    that.open4('');
                })
            }
        },
        submitForm(formName) {
            //
            var that=this;
            this.$axios({
                method:'POST',
                url:'http://111.230.128.231:8080/TTMS/playUpdate',
                params:that.ruleForm,
                headers:{
                'Content-type': 'application/x-www-form-urlencoded'
                },
                transformRequest: [function (data) {
                    let ret = ''
                    for (let it in data) {
                        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                    }
                    return ret
                }]
            }).then(function(response){
                that.open2('');
            }).catch(function(response){
                that.open4('');
            })
        },
        resetForm(formName) {
            //
            this.$refs[formName].resetFields();
        },
        open2(msg) {
            this.$message({
            message: msg,
            type: 'success'
            });
        },
        open4(msg) {
            this.$message.error(msg);
        },
    }
}
</script>

<style scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .form-insert span{
      color: #5F6260;
      margin: 10px;
      font-size: 14px
      }
  .form-insert input[type*='text'],.form-insert select{
      display: inline-block;
      height: 40px;
      width: 202px;
      padding: 0 15px;
      border:1px solid #dcdfe6;
      border-radius: 4px;
      color: #606266;
      line-height: 40px;
      box-sizing:border-box;
  }
  .form-insert p{
      margin: 10px 0;
  }
  .form-insert input[type*='submit']{
      margin: 10px;
      color: #fff;
      background-color: #409EFF;
      border:1px solid #dcdfe6;
      border-color: #409EFF;
      font-size: 14px;
      padding: 12px 20px;
      border-radius: 4px
  }
  .form-insert input[type*='submit']:hover{
      background-color: #35B3FF;
      cursor: pointer
  }
  .select_info{
      padding: 20px;
  }
  .select_info img{
      width: 160px;
      height:220px;
      float: left;
  }
  .select_info div{
      margin-left: 200px;
  }
  .select_info p{
      margin: 5px 20px;
      word-wrap: break-word; 
  }
  .el-form-item{
      margin-bottom: 11px
  }
</style>




